@font-face {
  font-family: 'Monument';
  src: url('./fonts/MonumentExtended-Ultrabold.otf') format('opentype'); }
@font-face {
  font-family: 'RF-Dewi';
  src: url('./fonts/RF-Dewi-Expanded-Semibold.otf') format('opentype'); }


$font-gray: #C4C4C4;
$font-light-gray: #B7B7B7;

$joystick-breakpoint: 1024px;
$problematic-breakpoint: 320px;

$slide-transition: .35s;


* {
  font-family: 'RF-Dewi', sans-serif;
  color: $font-light-gray;
  margin: 0;
  box-sizing: border-box; }

body {
  background-color: black; }

// @media screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape)
//   html
//     transform: rotate(-90deg)
//     transform-origin: left top
//     width: 100vh
//     overflow-x: hidden
