.desktop-only {
  @media screen and ( max-width: $joystick-breakpoint ) {
    display: none; } }
.mobile-only {
  @media screen and ( min-width: $joystick-breakpoint + 1px) {
    display: none; } }

.Viewer {
  background-color: #444444;
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  @extend .vingette;
  @extend .grain;
  canvas {
    width: 100%;
    height: 100%; }
  #zone-joystick {
    @extend .mobile-only;
    width: 100%;
    height: 40%;
    position: absolute;
    right: 0;
    bottom: 0;
    .back {
      background: rgba(0, 0, 0, 0) !important;
      box-shadow: inset 0 0 0px 4px white;
      opacity: .85 !important; }
    .front {
      opacity: .85 !important; }
    @media screen and ( max-width: 568px ) {
      height: 50%; } }
  .coins-collected {
    position: absolute;
    left: 45px;
    bottom: 25px;
    background-color: black;
    width: 160px;
    height: 50px;
    border-radius: 15px;
    padding-left: 70px;
    line-height: 50px;
    font-size: 30px;
    font-weight: 800px;
    color: white;
    &::before {
      content: "";
      position: absolute;
      width: 30px;
      height: 30px;
      top: 10px;
      left: 20px;
      border-radius: 20px;
      background-color: #FDFF86; }
    @media screen and ( max-width: 767px ) {
      left: 25px;
      bottom: 55px;
      width: 110px;
      height: 30px;
      border-radius: 8px;
      padding-left: 40px;
      line-height: 30px;
      font-size: 20px;
      &::before {
        width: 18px;
        height: 18px;
        top: 6px;
        left: 12px; } } } }



.landing {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  @media screen and ( max-width: $joystick-breakpoint ) {
    height: 60%; }
  &__scrollable {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: scroll;
    // z-index: 100000
    &__long-part {
      width: 100%;
      height: 300vh;
      .anchor {
        width: 100%;
        height: 33%; } } }
  &__content {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    overflow: hidden;
    &__h1 {
      font-family: 'Monument', sans-serif;
      font-weight: 900;
      text-align: center;
      font-size: 4.5vw;
      position: absolute;
      right: 5vw;
      top: 42%;
      transition: color $slide-transition, opacity $slide-transition, top $slide-transition;
      @media screen and ( max-width: $joystick-breakpoint ) {
        font-size: 9vw;
        right: 50vw;
        top: 17vh;
        transform: translateX(50%); } }
    &--0 {
      .landing__content__h1 {
        opacity: 1;
        color: rgb(120, 201, 143); } }
    &--1 {
      .landing__content__h1 {
        opacity: .8;
        color: rgb(101, 170, 120);
        top: 5vw; } }
    &--2 {
      .landing__content__h1 {
        opacity: .8;
        color: rgb(101, 170, 120);
        top: 10vw;
        @media screen and ( max-width: $joystick-breakpoint ) {
          top: 19vw; } } }

    &__description-0 {
      font-size: 18px;
      line-height: 2;
      text-align: center;
      position: absolute;
      width: 40vw;
      top: 30vh;
      right: 150vw;
      transition: right $slide-transition;
      @media screen and ( max-width: $joystick-breakpoint ) {
        width: 80vw;
        top: 10vh;
        font-size: 10px; } }
    &--0 {
      .landing__content__description-0 {
        right: 2.5vw;
        @media screen and ( max-width: $joystick-breakpoint ) {
          right: 50vw;
          transform: translateX(50%); } } }


    &__description-1 {
      font-size: 1vw;
      text-align: center;
      width: 30vw;
      position: absolute;
      top: 10vw;
      left: -50vw;
      transition: left $slide-transition;
      @media screen and ( max-width: $joystick-breakpoint ) {
        width: 60vw;
        font-size: 2vw;
        top: 65vw;
        transform: translateX(-50%); }
      @media screen and ( max-width: $problematic-breakpoint ) {
        top: 55vw; } }
    &--0 {
      .landing__content__description-1 {
        left: 10vw;
        @media screen and ( max-width: $joystick-breakpoint ) {
          left: 50vw; } } }

    &__description-2 {
      font-size: 12px;
      width: 30vw;
      min-width: 355px;
      position: absolute;
      top: 20vw;
      transition: right $slide-transition;
      @media screen and ( max-width: $joystick-breakpoint ) {
        width: 65vw;
        min-width: 100px;
        font-size: 1.5vw;
        top: 20vh;
        transform: translateX(50%); }
      @media screen and ( max-width: $problematic-breakpoint ) {
        width: 75vw;
        top: 17vh; } }
    &--0 {
      .landing__content__description-2 {
        right: -60vw;
        @media screen and ( max-width: $joystick-breakpoint ) {
          right: -100vw; } } }
    &--1 {
      .landing__content__description-2 {
        right: 10vw;
        @media screen and ( max-width: $joystick-breakpoint ) {
          right: 50vw; } } }
    &--2 {
      .landing__content__description-2 {
        right: 110vw;
        @media screen and ( max-width: $joystick-breakpoint ) {
          right: 180vw; } } }

    &__credits {
      font-size: 15px;
      line-height: 1.3;
      width: 20vw;
      position: absolute;
      top: 23vw;
      transition: right $slide-transition;
      @media screen and ( max-width: $joystick-breakpoint ) {
        font-size: 1.8vw;
        width: 60vw;
        top: 42vw;
        transform: translateX(50%); } }
    &--0 {
      .landing__content__credits {
        right: -60vw; } }
    &--1 {
      .landing__content__credits {
        right: -60vw; } }
    &--2 {
      .landing__content__credits {
        right: 13vw;
        @media screen and ( max-width: $joystick-breakpoint ) {
          right: 50vw; } } }
    span.green {
      color: rgba(155, 255, 183, 0.5); }

    &__links {
      width: 25vw;
      position: absolute;
      top: 5vh;
      transition: right $slide-transition;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      z-index: 10000;
      @media screen and ( max-width: $joystick-breakpoint ) {
        width: 80vw; }
      // @media screen and ( max-width: $problematic-breakpoint )
      //   top: 7vh
      a {
        text-decoration: none; }
      &__item {
        font-size: 25px;
        line-height: 1.3;
        padding: 0 5px;
        color: black;
        background-color: white;
        cursor: pointer;
        // display: inline-block
        @media screen and ( max-width: $joystick-breakpoint ) {
          font-size: 2vw; } } }
    &--0, &--1 {
      .landing__content__links {
        right: -60vw;
        @media screen and ( max-width: $joystick-breakpoint ) {
          right: -120vw; } } }
    &--2 {
      .landing__content__links {
        right: 13vw;
        @media screen and ( max-width: $joystick-breakpoint ) {
          right: 50vw;
          transform: translateX(50%); } } }
    span.green {
      color: rgba(155, 255, 183, 0.5); }



    &__slide {
      width: 100%;
      height: 100%;
      &__text {
        color: $font-gray;
        text-align: center;
        font-size: 24px;
        margin-bottom: 50vh;
        @media screen and ( max-width: 1500px ) {
          font-size: 16px; }
        @media screen and ( max-width: $joystick-breakpoint ) {
          font-size: 11px;
          margin-bottom: 0; }
        @media screen and ( max-width: 800px ) {
          font-size: 8px; } } }
    &__right-column {
      @media screen and ( max-width: $joystick-breakpoint ) {
        order: 1; } } } }



.controls-helper {
  background-color: white;
  width: 190px;
  height: 70px;
  @media screen and ( max-width: $joystick-breakpoint ) {
    height: 56px; }
  border-radius: 15px;
  position: absolute;
  bottom: 150px;
  @media screen and ( min-width: $joystick-breakpoint + 1px) {
    left: 45px;
    padding-left: 24px; }
  @media screen and ( max-width: $joystick-breakpoint ) {
    right: 15px;
    bottom: -50px;
    padding-right: 24px; }
  &__text {
    color: rgba(0, 0, 0, 0.6);
    font-size: 8px;
    line-height: 1.5;
    padding: 8px;
    &--desktop {
      @extend .desktop-only; }
    &--mobile {
      @extend .mobile-only; } }
  &__close {
    cursor: pointer;
    position: absolute;
    top: 0;
    width: 30px;
    height: 100%;
    @media screen and ( min-width: $joystick-breakpoint + 1px) {
      left: 0; }
    @media screen and ( max-width: $joystick-breakpoint ) {
      right: 0; }
    &::after, &::before {
      content: "";
      position: absolute;
      top: calc(50% - 2px);
      left: 8px;
      background-color: #343434;
      width: 12px;
      height: 1px; }
    &::after {
      transform: rotate(45deg); }
    &::before {
      transform: rotate(-45deg); } } }


.download-links {
  position: absolute;
  left: 50vw;
  transform: translateX(-50%);
  width: 12vw;
  min-width: 300px;
  z-index: 100000;
  transition: left $slide-transition;
  @media screen and ( min-width: $joystick-breakpoint + 1px ) {
    bottom: 25px; }
  @media screen and ( max-width: $joystick-breakpoint ) {
    top: 50vh; }
  @media screen and ( max-width: 768px - 1px ) {
    min-width: 150px;
    top: 42vh; }
  &__width-container {
    width: 100%;
    padding-top: 16.6%;
    &__abs-container {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      img {
        height: 100%;
        width: auto; } } } }

@media screen and ( max-width: $joystick-breakpoint ) {
  .landing__content--0,
  .landing__content--1, {
    .download-links {
      left: 150vw; } }
  .landing__content--2 {
    .download-links {
      left: 50vw; } } }


.video-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .85);
  cursor: pointer;
  z-index: 10000;
  &__container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 70%;
    padding-top: calc(100% * 0.5625);
    @media screen and ( max-width: $joystick-breakpoint ) {
      width: 90%;
      padding-top: calc(100% * 0.5625); }
    &__abs-container {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      iframe {
        width: 100%;
        height: 100%; } } } }

// .word-changer__height
//   height: 16px
//   @media screen and ( max-width: $joystick-breakpoint )
//     height: 8px

$word-changer-transition: .3s;
.word-changer {
  display: inline-block;
  position: relative;
  width: 160px;
  height: 16px;
  margin: 0 10px;
  @media screen and ( max-width: $joystick-breakpoint ) {
    height: 8px;
    width: 80px; }
  &::after {
    content: "";
    position: absolute;
    top: 10px;
    left: -5px;
    height: 1px;
    width: calc(100% + 10px);
    background-color: #B7B7B7;
    @media screen and ( max-width: $joystick-breakpoint ) {
      top: 6px; } }
  &__container {
    position: absolute;
    height: 500%;
    width: 100%;
    left: 0;
    top: -200%;
    overflow: hidden;
    .item {
      text-align: left;
      height: 16px;
      @media screen and ( max-width: $joystick-breakpoint ) {
        height: 8px; }
      line-height: 1;
      transition: height $word-changer-transition, opacity $word-changer-transition;
      opacity: 0;
      &:nth-child(1) {
        height: 0;
        opacity: 0; }
      &:nth-child(5) {
        height: 16px;
        @media screen and ( max-width: $joystick-breakpoint ) {
          height: 8px; } }
      &:nth-child(3), &:nth-child(5) {
        opacity: .5; }
      &:nth-child(4) {
        opacity: 1; }
      &:nth-child(1n + 6) {
        height: 0;
        opacity: 0; } } } }
.word-changer__container--shrinked .item {
  &:nth-child(1) {
    height: 16px;
    @media screen and ( max-width: $joystick-breakpoint ) {
      height: 8px; } }
  &:nth-child(5) {
    height: 0;
    opacity: 0; }
  &:nth-child(3) {
    opacity: 1; }
  &:nth-child(2), &:nth-child(4) {
    opacity: .5; } }






@media screen and (min-width: 320px) and (max-width: 812px) and (orientation: landscape) {
  .landing__content__h1 {
    font-size: 1.5vw !important;
    top: 10vh !important;
    right: 10vw !important;
    opacity: .8 !important;
    color: rgb(120, 201, 143) !important; }
  .coins-collected {
    bottom: 7vh !important; }
  .landing__content__description-0,
  .landing__content__description-1,
  .landing__content__description-2,
  .landing__content__credits,
  .landing__content__links,
  .controls-helper,
  .download-links,
  .video-modal {
    display: none !important; } }



.buttons {
  position: fixed;
  bottom: 0;
  left: 0;
  opacity: .5;
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  z-index: 10000;
  &__item {
    cursor: pointer;
    font-size: 10px;
    padding: 10px;
    background-color: black; } }



.center-con {
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center; }






.arrow {
  width: 50px;
  height: 50px;
  cursor: pointer;
  position: absolute;
  top: 50vh;
  transform: translateY(-50%);
  &__item {
    position: absolute;
    top: 17px;
    left: 17px;
    width: 16px;
    height: 16px;
    animation-name: bounceRight;
    animation-duration: 1.4s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      border-top: 3px solid white;
      border-right: 3px solid white;
      transform: rotate(45deg); }
    &--segunda {
      animation-delay: 0.2s;
      margin-left: 12px; } }
  &:not(&--left) {
    right: 10px; }
  &--left {
    left: 10px;
    .arrow__item {
      animation-name: bounceLeft;
      &::after {
        transform: rotate(-135deg); }
      &--segunda {
        margin-left: -12px; } }
    @keyframes bounceRight {
      0% {
        opacity: 1;
        transform: translateX(0px) scale(1); }
      25% {
        opacity: 0;
        transform: translateX(10px) scale(0.9); }
      26% {
        opacity: 0;
        transform: translateX(-10px) scale(0.9); }
      55% {
        opacity: 1;
        transform: translateX(0px) scale(1); } }
    @keyframes bounceLeft {
      0% {
        opacity: 1;
        transform: translateX(0px) scale(1); }
      25% {
        opacity: 0;
        transform: translateX(-10px) scale(0.9); }
      26% {
        opacity: 0;
        transform: translateX(10px) scale(0.9); }
      55% {
        opacity: 1;
        transform: translateX(0px) scale(1); } } } }






.vingette {
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: inset 0 0 500px black;
    background: linear-gradient(90deg, rgba(0,0,0,0) 40%, rgba(0,0,0,0.75) 75%);
    @media screen and ( max-width: $joystick-breakpoint ) {
      background: linear-gradient(0deg, rgba(0,0,0,0) 60%, rgba(0,0,0,0.75) 95%); } } }

.grain {
  // position: relative
  overflow: hidden;
  &::after {
    content: "";
    background-image: url(https://sun9-21.userapi.com/c855236/v855236973/21ef90/A4K_iEg4g6Q.jpg);
    height: 300%;
    width: 300%;
    opacity: 0.05;
    position: absolute;
    top: -50%;
    left: -50%;
    // z-index: 100000
    // background-size: 5%
    animation: grain 8s steps(4) infinite;
    @keyframes grain {
      0%, 100% {
        transform: translate(0, 0); }
      10% {
        transform: translate(-.5%, -1.0%); }
      20% {
        transform: translate(-1.5%, .5%); }
      30% {
        transform: translate(.7%, -2.5%); }
      40% {
        transform: translate(-.5%, 2.5%); }
      50% {
        transform: translate(-1.5%, 2.0%); }
      60% {
        transform: translate(1.5%, 0%); }
      70% {
        transform: translate(0%, 1.5%); }
      80% {
        transform: translate(3%, 3.5%); }
      90% {
        transform: translate(-1.0%, 1.0%); } } } }





